import React from "react";
import "../widget/widget.scss";
import ArrowDropUpIcon from "@mui/icons-material/ArrowDropUp";
import PersonRoundedIcon from "@mui/icons-material/PersonRounded";
import AddShoppingCartRoundedIcon from "@mui/icons-material/AddShoppingCartRounded";
import SavingsRoundedIcon from "@mui/icons-material/SavingsRounded";
import AccountBalanceWalletRoundedIcon from "@mui/icons-material/AccountBalanceWalletRounded";
import { NavLink } from "react-router-dom";

const Widget = ({ type, allUsers, allOrders, earned }) => {
  let data;
  switch (type) {
    case "user":
      data = {
        amount: allUsers.length,
        title: "USERS",
        isMoney: false,
        link: "See all users",
        href: "/list",
        icon: <PersonRoundedIcon className="bgIcon" />,
      };
      break;
    case "order":
      data = {
        amount: allOrders.length,
        title: "ORDERS",
        isMoney: false,
        link: "Check orders",
        href: "/orders",
        icon: (
          <AddShoppingCartRoundedIcon
            className="bgIcon"
            style={{ backgroundColor: "48cae4" }}
          />
        ),
      };
      break;
    case "earning":
      data = {
        amount: earned,
        title: "EARNINGS",
        isMoney: true,
        link: "",
        icon: (
          <SavingsRoundedIcon
            className="bgIcon"
            style={{ backgroundColor: "e63946" }}
          />
        ),
      };
      break;
    case "balance":
      data = {
        amount: earned,
        title: "BALANCE",
        isMoney: true,
        link: "",
        icon: (
          <AccountBalanceWalletRoundedIcon
            className="bgIcon"
            style={{ backgroundColor: "fcbf49" }}
          />
        ),
      };
      break;
    default:
      break;
  }

  return (
    <div className="widget">
      <div className="left-section">
        <h3 className="heading">{data.title}</h3>
        <p className="user-count">
          {data.isMoney && "$"} {data.amount}
        </p>
        <NavLink to={data.href} className="view-all-link">
          {data.link}
        </NavLink>
      </div>
      <div className="right-section">{data.icon}</div>
    </div>
  );
};

export default Widget;
