import React, { useEffect } from "react";
import "./home.scss";
import Sidebar from "../../components/sidebar/Sidebar";
import Navbar from "../../components/navbar/Navbar";
import Widget from "../../components/widget/Widget";
import Featured from "../../components/featured/Featured";
import Chart from "../../components/chart/Chart";
import Table from "../../components/table/Table";
import { useUserContext } from "../../contexts/userContext";
import { useOrderContext } from "../../contexts/orderContext";

const Home = () => {
  const { allUsers, fetchUsers } = useUserContext();
  const { allOrders, fetchOrders } = useOrderContext();

  useEffect(() => {
    fetchOrders("http://localhost:5000/orders");
    fetchUsers("http://localhost:5000/api/users");
  }, []);

  // orders getting today
  const today = new Date();
  today.setHours(0, 0, 0, 0);

  const todayOrders = allOrders.filter((order) => {
    const orderDate = new Date(order.date);
    orderDate.setHours(0, 0, 0, 0);
    return orderDate.getTime() === today.getTime();
  });

  // earnings made today
  const approvedOrdersToday = todayOrders.filter(
    (order) => order.status === "approved"
  );

  const earningsToday = approvedOrdersToday.reduce(
    (total, order) => total + order.moneyTransfered,
    0
  );

  // approved orders with the total amount
  const approvedOrders = allOrders.filter(
    (order) => order.status === "approved"
  );
  const earned = approvedOrders.reduce(
    (total, order) => total + order.moneyTransfered,
    0
  );

  return (
    <div className="home">
      <Sidebar />
      <div className="homeContainer">
        {/* <Navbar /> */}
        <div className="widgets">
          <Widget allUsers={allUsers} type="user" />
          <Widget allOrders={allOrders} type="order" />
          <Widget earned={earned} type="earning" />
          <Widget earned={earned} type="balance" />
        </div>
        <div className="charts">
          <Featured earningsToday={earningsToday} />
          {/* <Chart aspect={2 / 1} title={"Last 6 Months"} /> */}
        </div>
        <div className="listContainer">
          <div className="listTitle">Today Orders</div>
          <Table orders={todayOrders} />
        </div>
      </div>
    </div>
  );
};

export default Home;
