import React, { createContext, useContext, useEffect, useReducer } from "react";
import filterUserReducer from "../reducers/filterUserReducer";
import { useUserContext, userContext } from "./userContext";

const filterUserContext = createContext();

const initialState = {
  filterUsers: [],
  allUsers: [],
  sortValue: "none",
  sortBy: "firstName",
  searchedText: "",
  searchedBy: "firstName",
};
const FilterUserContextProvider = ({ children }) => {
  const { allUsers } = useUserContext();
  const [state, dispatch] = useReducer(filterUserReducer, initialState);

  console.log(state.sortBy);
  // to store all user in filter users
  useEffect(() => {
    dispatch({ type: "LOAD_FILTER_USERS", payload: allUsers });
  }, [allUsers]);

  // to set search by value
  const searchBy = () => {
    dispatch({ type: "SET_SEARCHBY_VALUE" });
  };

  // set search text value
  const searchedText = (event) => {
    const value = event.target.value;
    dispatch({ type: "SET_SEARCHED_TEXT", payload: value });
  };

  // works every time when there is change in searchedText or searchedBy
  useEffect(() => {
    dispatch({ type: "SEARCH_USERS" });
  }, [state.searchedText, state.searchedBy]);

  // for sorting the user names
  const setSortValue = () => {
    dispatch({ type: "SET_SORT_VALUE" });
  };

  // which can be sort firstName or lastName
  const sortBy = () => {
    dispatch({ type: "SORT_BY" });
  };

  useEffect(() => {
    dispatch({ type: "SORT_USERS" });
  }, [state.sortValue, state.sortBy]);

  return (
    <filterUserContext.Provider
      value={{ ...state, searchBy, searchedText, setSortValue, sortBy }}
    >
      {children}
    </filterUserContext.Provider>
  );
};

const useUserFilterContext = () => {
  return useContext(filterUserContext);
};

export { FilterUserContextProvider, useUserFilterContext };
