import React, { useEffect, useState } from "react";
import { useUserFilterContext } from "../../contexts/filterUserContext";
import { NavLink } from "react-router-dom";
import axios from "axios";

const ProductDataTable = () => {
  const [allProducts, setAllProducts] = useState([]);

  // fetch all the products
  const fetchUsers = async (url) => {
    try {
      const res = await axios.get(url);
      const users = await res.data;
      setAllProducts(users);
    } catch (error) {}
  };

  useEffect(() => {
    fetchUsers("http://localhost:5000/api/products");
  }, []);

  return (
    <div className="userListContainer">
      {/* search section  */}

      {/* user grid  */}
      <div className="user-grid">
        <div className="user-grid-header">
          <div className="header-item">Category</div>
          <div className="header-item">Serial No.</div>
          <div className="header-item">Name</div>
          <div className="header-item">Price</div>
          <div className="header-item">Quantity</div>
        </div>
        {allProducts.map((products) => (
          <div className="user-grid-row" key={products.id}>
            {/* <div className="grid-item">{products._id}</div> */}
            <div className="grid-item">{products.category}</div>
            <div className="grid-item">{products.serialNo}</div>
            <div className="grid-item">{products.name}</div>
            <div className="grid-item">{products.price}</div>
            <div className="grid-item">{products.quantity}</div>
            {/* <NavLink to={`/user/${products._id}`} className="grid-item">
              <button className="view-button"> View </button>
            </NavLink> */}
          </div>
        ))}
      </div>
    </div>
  );
};

export default ProductDataTable;
