import { createContext, useContext, useEffect, useReducer } from "react";
import axios from "axios";
import reducerForUser from "../reducers/userReducer";
const API = "http://localhost:5000/api/users";

const userContext = createContext();
const initialState = {
  singleUser: {},
  isSingleLoading: false,
  allUsers: [],
  isLoading: false,
  isError: false,
  search: {
    searchedText: "",
    searchedBy: "by name",
  },
};

const UserProvider = ({ children }) => {
  const [state, dispatch] = useReducer(reducerForUser, initialState);

  // fetch all the users from the products data base
  const fetchUsers = async (url) => {
    dispatch({ type: "SET_LOADING" });
    try {
      const res = await axios.get(url);
      const users = await res.data;
      dispatch({ type: "SET_API_DATA", payLoad: users });
    } catch (error) {
      dispatch({ type: "API_ERROR" });
    }
  };

  const fetchSingleUser = async (url) => {
    dispatch({ type: "SET_SINGLE_LOADING" });
    try {
      const res = await axios.get(url);
      const singleUser = await res.data;
      dispatch({ type: "SET_SINGLE_DATA", payLoad: singleUser });
    } catch (error) {
      dispatch({ type: "API_ERROR" });
    }
  };

  //use effect
  useEffect(() => {
    fetchUsers(API);
  }, []);

  return (
    <userContext.Provider value={{ ...state, fetchSingleUser, fetchUsers }}>
      {children}
    </userContext.Provider>
  );
};

const useUserContext = () => {
  return useContext(userContext);
};

export { UserProvider, useUserContext };
