import React, { useEffect, useState } from "react";
import "./datatable.scss";
import DeleteIcon from "@mui/icons-material/Delete";
import { useUserContext } from "../../contexts/userContext";
import { useUserFilterContext } from "../../contexts/filterUserContext";
import { NavLink } from "react-router-dom";

const Datatable = () => {
  const { filterUsers, searchBy, searchedText, setSortValue, sortBy } =
    useUserFilterContext();

  return (
    <div className="userListContainer">
      {/* search section  */}
      <div className="searchSection">
        <div className="user-grid-search">
          <input
            onChange={(event) => {
              searchedText(event);
            }}
            type="text"
            placeholder="Search user..."
          />
          <select
            onClick={() => {
              searchBy();
            }}
            id="searchBy"
          >
            <option value="firstName">by first name</option>
            <option value="lastName">by last name</option>
            <option value="_id">by id</option>
            <option value="email">by email</option>
            <option value="phone">by phone</option>
          </select>
        </div>

        <div className="sort-value-selection">
          <select
            onClick={() => {
              sortBy();
            }}
            id="sortBy"
          >
            <option value="firstName">Sort First Name by:</option>
            <option value="lastName">Sort Last Name by:</option>
          </select>
          <select
            onClick={() => {
              setSortValue();
            }}
            id="sortValue"
          >
            <option value="none">none</option>
            <option value="a-z">a-z</option>
            <option value="z-a">z-a</option>
          </select>
        </div>
      </div>

      {/* user grid  */}
      <div className="user-grid">
        <div className="user-grid-header">
          <div className="header-item">ID</div>
          <div className="header-item">First Name</div>
          <div className="header-item">Last Name</div>
          <div className="header-item">Phone</div>
          <div className="header-item">Email</div>
          <div className="header-item">_</div>
        </div>
        {filterUsers.map((user) => (
          <div className="user-grid-row" key={user.id}>
            <div className="grid-item overflow-text">{user._id}</div>
            <div className="grid-item">{user.firstName}</div>
            <div className="grid-item">{user.lastName}</div>
            <div className="grid-item">{user.phone}</div>
            <div className="grid-item overflow-text">{user.email}</div>
            <NavLink to={`/user/${user._id}`} className="grid-item">
              <button className="view-button"> View </button>
            </NavLink>
          </div>
        ))}
      </div>
    </div>
  );
};

export default Datatable;
