import React, { useEffect } from "react";
import Sidebar from "../../components/sidebar/Sidebar";
import Navbar from "../../components/navbar/Navbar";
import "./single.scss";
import Chart from "../../components/chart/Chart";
import Table from "../../components/table/Table";
import { Avatar, AvatarBadge, AvatarGroup, Stack } from "@chakra-ui/react";
import { useUserContext } from "../../contexts/userContext";
import { useParams } from "react-router-dom";
import { useOrderContext } from "../../contexts/orderContext";

const Single = () => {
  const { fetchSingleUser, singleUser } = useUserContext();
  const API = "http://localhost:5000/api/users";
  const { id } = useParams();

  useEffect(() => {
    fetchSingleUser(`${API}/${id}`);
  }, []);

  // orders
  const { allOrders } = useOrderContext();

  const filterOrders = allOrders.filter(
    (order) => order.email === singleUser.email
  );

  return (
    <div className="single">
      <Sidebar />
      <div className="singleContainer">
        {/* <Navbar /> */}
        <div className="top">
          <div className="left">
            <div className="topSection">
              <h1 className="title">Info.</h1>
            </div>
            <div className="bottomSection">
              <div className="item">
                {/* <img
                  src="https://images.pexels.com/photos/733872/pexels-photo-733872.jpeg?auto=compress&cs=tinysrgb&dpr=3&h=750&w=1260"
                  alt=""
                  className="itemImg"
                /> */}
              </div>
              <div className="details">
                <p className="userName">
                  {singleUser.firstName} {singleUser.lastName}
                </p>
                <p>
                  <strong>Email:</strong> {singleUser.email}
                </p>
                <p>
                  <strong>Phone:</strong> {singleUser.phone}
                </p>
                <p>
                  <strong>Orders Placed:</strong> {filterOrders.length}
                </p>
              </div>
            </div>
          </div>
          <div className="right">
            <Chart
              aspect={5 / 1}
              title={`Amount spend by ${singleUser.firstName} in $`}
              orders={filterOrders}
            />
          </div>
        </div>
        <div className="bottom">
          <div className="title"> Placed Orders </div>
          <Table orders={filterOrders} />
        </div>
      </div>
    </div>
  );
};

export default Single;
