const filterUserReducer = (state, action) => {
  switch (action.type) {
    case "LOAD_FILTER_USERS":
      return {
        ...state,
        filterUsers: [...action.payload],
        allUsers: [...action.payload],
      };

    case "SET_SEARCHED_TEXT":
      return {
        ...state,
        searchedText: action.payload,
      };

    // search users
    case "SEARCH_USERS":
      let { allUsers } = state;
      let tempUsers = [...allUsers];

      const { searchedText, searchedBy } = state;
      tempUsers = tempUsers.filter((cur) => {
        return cur[searchedBy].toLowerCase().startsWith(searchedText);
      });

      return {
        ...state,
        filterUsers: tempUsers,
      };

    // sort users
    case "SORT_USERS":
      let sortedUsers;
      let tempSortUsers = [...state.filterUsers];
      const { sortBy } = state;

      if (state.sortValue === "a-z") {
        sortedUsers = tempSortUsers.sort((a, b) => {
          return a[sortBy].localeCompare(b[sortBy]);
        });
      } else if (state.sortValue === "z-a") {
        sortedUsers = tempSortUsers.sort((a, b) => {
          return b[sortBy].localeCompare(a[sortBy]);
        });
      } else {
        sortedUsers = [...state.allUsers];
      }

      return {
        ...state,
        filterUsers: sortedUsers,
      };

    // get the search by value
    case "SET_SEARCHBY_VALUE":
      let searchByValue = document.getElementById("searchBy");
      let value = searchByValue.options[searchByValue.selectedIndex].value;
      return {
        ...state,
        searchedBy: value,
      };

    // set sort value
    case "SET_SORT_VALUE":
      let sortValue = document.getElementById("sortValue");
      let setSortValue = sortValue.options[sortValue.selectedIndex].value;
      return {
        ...state,
        sortValue: setSortValue,
      };

    // sort by
    case "SORT_BY":
      let sortByValue = document.getElementById("sortBy");
      let setSortByValue = sortByValue.options[sortByValue.selectedIndex].value;
      return {
        ...state,
        sortBy: setSortByValue,
      };
  }
};

export default filterUserReducer;
