import React from "react";
import "./featured.scss";
import { CircularProgressbar } from "react-circular-progressbar";
import "react-circular-progressbar/dist/styles.css";
import MoreVertRoundedIcon from "@mui/icons-material/MoreVertRounded";
import KeyboardArrowUpRoundedIcon from "@mui/icons-material/KeyboardArrowUpRounded";

const Featured = ({ earningsToday }) => {
  return (
    <div className="featured">
      <div className="top-section">
        <h3 className="total-revenue-heading">Total Revenue </h3>
      </div>
      <div className="bottom-section">
        <div className="progress-bar-section">
          <CircularProgressbar value={53} text={"53%"} />
        </div>
        <div className="sales-today-section">
          <p className="sales-today-text">Total Sales From Today's Orders</p>
          <p className="sales-today-money">{earningsToday}$</p>
        </div>
        <div className="previous-transactions-section">
          <p className="previous-transactions-text">
            Previous transactions processing. Last payments may not be included.
          </p>
        </div>
        <div className="section-row">
          <div className="section-item">
            <h4>TARGET</h4>
            <div className="item-price">
              <KeyboardArrowUpRoundedIcon />
              <p className="targetProhit">$100</p>
            </div>
          </div>
          <div className="section-item">
            <h4>Last Week</h4>
            <div className="item-price">
              <KeyboardArrowUpRoundedIcon />
              <p>$200</p>
            </div>
          </div>
          <div className="section-item">
            <h4>Last Month</h4>
            <div className="item-price">
              <KeyboardArrowUpRoundedIcon />
              <p>$300</p>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default Featured;
