import React from "react";
import "./sidebar.scss";
//icons
import DashboardIcon from "@mui/icons-material/Dashboard";
import PersonRoundedIcon from "@mui/icons-material/PersonRounded";
import StoreRoundedIcon from "@mui/icons-material/StoreRounded";
import ContentPasteRoundedIcon from "@mui/icons-material/ContentPasteRounded";
import SystemSecurityUpdateWarningRoundedIcon from "@mui/icons-material/SystemSecurityUpdateWarningRounded";
import PersonPinRoundedIcon from "@mui/icons-material/PersonPinRounded";
import { NavLink } from "react-router-dom";

const Sidebar = () => {
  return (
    <div className="sidebar">
      <div className="top">
        <span className="logo">
          BackEnd
          <p>
            backend website with a dashboard to view and manage user information
            and orders for supermart website.
          </p>
        </span>
      </div>
      <div className="center">
        <ul>
          <p className="title">MAIN</p>
          <li>
            <DashboardIcon className="icon" />
            <NavLink to={"/"}>
              <span>Dashboard</span>
            </NavLink>
          </li>
          <p className="title">LISTS</p>
          <li>
            <PersonRoundedIcon className="icon" />
            <span>
              {" "}
              <NavLink to={"/list"}>Users</NavLink>{" "}
            </span>
          </li>
          <li>
            <ContentPasteRoundedIcon className="icon" />
            <span>
              <NavLink to={"/orders"}>Orders</NavLink>
            </span>
          </li>
          <li>
            <StoreRoundedIcon className="icon" />
            <NavLink to={"/products"}>
              <span>Products</span>
            </NavLink>
          </li>

          <p className="title">SERVICES</p>
          <li>
            <SystemSecurityUpdateWarningRoundedIcon className="icon" />
            <span>System Health</span>
          </li>

          <p className="title">USER</p>
          <li>
            <PersonPinRoundedIcon className="icon" />
            <span>Profile</span>
          </li>
        </ul>
      </div>
    </div>
  );
};

export default Sidebar;
