// OrderDatatable.js
import React, { useEffect, useState } from "react";
import { useOrderContext } from "../../contexts/orderContext";
import { NavLink } from "react-router-dom";
import axios from "axios";
import "./orderDatatable.scss";

const OrderDatatable = () => {
  const { allOrders, fetchOrders } = useOrderContext();
  const [orderStatus, setOrderStatus] = useState("pending");
  const [filteredOrders, setFilteredOrders] = useState([]);

  // Filter orders
  useEffect(() => {
    const updatedFilteredOrders = allOrders.filter(
      (order) => order.status === orderStatus
    );
    setFilteredOrders(updatedFilteredOrders);
  }, [allOrders, orderStatus]);

  // Format timestamp
  function formatTimestamp(timestamp) {
    const date = new Date(timestamp);
    const currentDate = new Date();

    const options = {
      weekday: "long",
      year: "numeric",
      month: "long",
      day: "numeric",
      hour: "numeric",
      minute: "numeric",
    };

    if (date.toDateString() === currentDate.toDateString()) {
      return "Today";
    } else {
      const nextDay = new Date(currentDate);
      nextDay.setDate(nextDay.getDate() + 1);
      if (date.toDateString() === nextDay.toDateString()) {
        return "Tomorrow";
      } else {
        return date.toLocaleDateString("en-US", options);
      }
    }
  }

  const approveOrder = async (orderId) => {
    try {
      await axios.put("http://localhost:5000/approveOrder", {
        id: orderId,
        updateStatus: "approved",
      });
      fetchOrders("http://localhost:5000/orders");
    } catch (error) {
      console.log(error);
    }
  };

  const rejectOrder = async (orderId) => {
    try {
      await axios.put("http://localhost:5000/rejectOrder", {
        id: orderId,
        updateStatus: "rejected",
      });
      fetchOrders("http://localhost:5000/orders");
    } catch (error) {
      console.log(error);
    }
  };

  const handleOrderStatusChange = (status) => {
    setOrderStatus(status);
  };

  return (
    <div className="userListContainer">
      <div className="buttons-container">
        <div>
          <button
            className={`buttons ${
              orderStatus === "pending" ? "active pending-button" : ""
            }`}
            onClick={() => handleOrderStatusChange("pending")}
          >
            Pending Orders
          </button>

          <button
            className={`buttons ${
              orderStatus === "approved" ? "active approved-button" : ""
            }`}
            onClick={() => handleOrderStatusChange("approved")}
          >
            Approved Orders
          </button>
          <button
            className={`buttons ${
              orderStatus === "rejected" ? "active rejected-button" : ""
            }`}
            onClick={() => handleOrderStatusChange("rejected")}
          >
            Rejected Orders
          </button>
        </div>

        <div style={{ fontSize: "20px" }}>
          {filteredOrders.length} {orderStatus} Orders
        </div>
      </div>
      <div className="user-grid">
        <div className="user-grid-header">
          <div className="header-item">Email</div>
          <div className="header-item">Phone</div>
          <div className="header-item">Amount</div>
          <div className="header-item">Date</div>
          <div className="header-item">Status</div>
          <div className="header-item">_</div>
          <div className="header-item">_</div>
        </div>
        {filteredOrders.map((order) => (
          <div className="user-grid-row" key={order.id}>
            <div className="grid-item">{order.email}</div>
            <div className="grid-item">{order.phone}</div>
            <div className="grid-item">{order.moneyTransfered}$</div>
            <div className="grid-item">{formatTimestamp(order.date)}</div>
            <div className="grid-item">{order.status}</div>
            <NavLink className="grid-item">
              {order.status !== "approved" && order.status !== "rejected" && (
                <button
                  onClick={() => approveOrder(order._id)}
                  className="view-button"
                >
                  Approve
                </button>
              )}
            </NavLink>
            <NavLink className="grid-item">
              {order.status !== "approved" && order.status !== "rejected" && (
                <button
                  onClick={() => rejectOrder(order._id)}
                  className="view-button"
                >
                  Reject
                </button>
              )}
            </NavLink>
          </div>
        ))}
      </div>
    </div>
  );
};

export default OrderDatatable;
